import { graphql, Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import style from "../sass/modules/index.module.sass"
import { RichText, Date } from "prismic-reactjs"
import Img from "gatsby-image"
import React, { Component } from "react"
import Slider from "react-slick"

export default ({ data }) => {
  const archives = data.prismic.allArchives.edges
  // const settings = {
  //   dots: true,
  //   infinite: true,
  //   speed: 500,
  //   slidesToShow: 1,
  //   slidesToScroll: 1,
  //   autoplay: true,
  // };
  class SimpleSlider extends Component {
    render() {
      const settings = {
        dots: false,
        arrows: false,
        infinite: true,
        speed: 1000,
        easing: `ease-in-out`,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 5000,
        touchMove: true,
        touchThreshold: 5,
        verticle: false,
        rows: 1,
      };
      return (
        <Slider {...settings} className={style.slider}>
          {archives.map(archive => {
            return (
              <Link to={'/archive/' + archive.node._meta.uid} key={archive.node._meta.id} className={style.featuredArchive}>
                <Img className={style.image} fluid={archive.node.hero_imageSharp.childImageSharp.fluid} alt={archive.node.hero_image.alt}/>
                <div to={'/archive/' + archive.node._meta.uid} key={archive.node._meta.id} className={style.featuredArchive}>
                  <div className={style.text}>
                    <h2>{RichText.asText(archive.node.title)}</h2>
                    <p>{archive.node.archive_number && <> Project {archive.node.archive_number}</> }{archive.node.year && <> <br/>Archived {Date(archive.node.year).getFullYear()}</>}</p>
                  </div>
                </div>
              </Link>
            )
          })}
        </Slider>
      );
    }
  }
  return (
    <Layout>
      <SEO title="Home" />
      <SimpleSlider/>
    </Layout>
  )
}


export const pageQuery = graphql`
  query SliderDataNew {
    prismic {
      allArchives(sortBy: archive_number_DESC first: 5) {
        edges {
          node {
            title
            location
            year
            archive_number
            services
            hero_image
            hero_imageSharp {
              id
              uid
              childImageSharp {
                id
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            _meta {
              id
              uid
            }
          }
        }
      }
    }
  }
`
